<template>
  <el-row :gutter="20" class="bank-card-info">
    <el-col :span="16">
      <div class="bind-list">
        <el-card v-if="cards.length === 0" class="card-box add-card">
          <span class="el-icon-circle-plus">Add bound bank card</span>
        </el-card>
        <el-card class="card-box" v-for="(card, index) in cards" :key="index">
          <el-form
            :ref="'form' + index"
            label-width="100px"
            label-position="left"
            :model="card"
          >
            <el-form-item :label="$t('personalCenter.bank')">
              <span>{{ card.bankName }}</span>
            </el-form-item>
            <el-form-item :label="$t('personalCenter.accountName')">
              <span>{{ card.accountName }}</span>
            </el-form-item>
            <el-form-item :label="$t('personalCenter.bankCardNumber')">
              <span>{{ card.bankCardNumber }}</span>
            </el-form-item>
            <el-form-item :label="$t('personalCenter.bankBranch')">
              <span>{{ card.bankBranch }}</span>
              <a
                class="operation"
                href="javascript:;"
                v-on:click="delBankBoundCard(card.cardId)"
              >
                <i class="el-icon-delete" />
              </a>
            </el-form-item>
          </el-form>
        </el-card>
      </div>
    </el-col>
    <el-col :span="8">
      <h3 class="help-title">{{ $t("personalCenter.BankSettingNotice") }}</h3>
      <div class="sub-help no-bottom-border">
        <p>
          1.After the bank card is successfully added, no information such as
          your complete bank account number and account name will appear in any
          area of the platform.
        </p>
        <p>2.Each platform account can add up to five bank cards.</p>
        <p>
          3.Only one bank card with the same cardholder name can be added to
          each account.
        </p>
        <p>
          4.After adding / modifying bank card information, it takes 1 hour to
          withdraw cash.
        </p>
        <p>5.Withdrawal can only be made with a bank card.</p>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "userLimit",
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["bindingBankCard"]),
    cards() {
      console.log(this.bindingBankCard);
      return this.bindingBankCard;
    }
  },
  methods: {
    ...mapActions(["deleteBankCard"]),
    delBankBoundCard(cardId) {
      this.$eventHub
        .$off("check-bank-card-success")
        .$once("check-bank-card-success", data => {
          const checkedToken = data.checkedToken;
          const checkedCardId = data.cardId;
          if (cardId !== checkedCardId) {
            console.log(cardId, checkedCardId, "cards");
            this.$message({
              type: "error",
              message: this.$t("fundAlertComponents.notTheSameCardNotice")
            });
          } else {
            this.deleteBankCard({
              cardId,
              checkedToken
            }).catch(err => {
              this.$message({
                type: "error",
                message: err
              });
            });
          }
        });
      this.$fundAlertCheckBankCard({ type: 3 });
    }
  },
  created() {}
};
</script>

<style lang="sass">
@import "../../common/css/color.sass"
.bank-card-info
  .bind-list
    margin-top: 20px
    display: -webkit-box
    display: flex
    flex-wrap: wrap
    -webkit-box-orient: horizontal
    -webkit-box-direction: normal
    flex-direction: row
    min-height: 210px
  .el-card
    color: #777
    height: 190px
    width: 48%
    margin-right: 4%
    box-sizing: border-box
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.08);
    -webkit-transition-duration: 0.3s
    transition-duration: 0.3s
    margin-bottom: 30px
    display: -webkit-box
    display: flex
    -webkit-box-orient: vertical
    -webkit-box-direction: normal
    flex-direction: column
    &.add-card
      user-select: none
      cursor: pointer
      border: 1px dashed #ccc
      box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.08)
      display: -webkit-box
      display: flex
      align-items: center
      justify-content: center
    .el-form-item
      margin: 0
  a.operation
   text-align: right
   position: absolute
   font-size: 18px
   right: 0
   top: 5px
   &:hover
     color: var(--primary)
</style>
